@charset "utf-8";

@import '../assets/fonts/fonts.scss';

.NavWrap {
    width: 1440px;
    background-color: #fff;
    height: 80px;
    margin: 0 auto;

    box-shadow: 0px 0px 5px #ccc;

    display: flex;
    flex-direction: column;
    justify-content: center;

    position: relative;
    z-index: 100;

    transition: 0.5s;
}

.NavLarge{
    background-image: url('../assets/images/header_bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    // background-position-y: -50px;
    height: 100px;

    .linkWrap {
        background-color: rgba($color: #BFD1E5, $alpha: 0.7);
        padding: 15px 0;
    }
}

.Nav {
    width: 1440px;
    margin: 0 auto;

    
    padding: 0 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.logo {
    width: 180px;
}

.linkWrap{
    width: 800px;

    ul{
        display: flex;
        flex-direction: row;
        justify-content: center;
        
        list-style: none;


        li {
            font-size: 20px;
            
            font-weight: bold;
            font-family: 'NotoSansKR', 'NotoSans', sans-serif;
            letter-spacing: 1.5px;

            
        
            a {
                text-decoration: none;
                color: #111;
                transition: 0.5s;
                white-space: nowrap;
            }
            a:hover{
                color: green;

            }
        }
    }


    img{
        width: 8px;
        margin: 0 20px;
        opacity: 80%;
    }

}


.buttonWrap {

    display: flex;
    flex-direction: row;
    gap: 10px;
}



.authButton{
    background-color: #296498;

    // box-shadow: 0px 0px 5px #666;

    // width: 114px;
    // height: 38px;

    width: 100px;
    height: 34px;
    border-radius: 20px;
    

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    align-items: center;
    padding: 0px;

    img {
        width: 25px;
    }


    a {
        text-decoration: none;
        color: #fff;
        width: 50px;

        font: {
            family: 'NotoSansKR', 'NotoSans', sans-serif;
            // weight: bold;
            size: 14px;
        }
    }
        
}


.main {
    
}


.subTitleWrap {
    
    width: 1440px;
    margin: 0 auto;
    height: 120px;

    background: {
        image: url('../assets/images/header_bg.svg');
        repeat: no-repeat;
        size: cover;
        position: left 35%;
    }
    
    display: flex;
    
}


.subTitle {
    position: relative;
    bottom: 0;
    width: 100%;
    margin: 0 auto;

    display: grid;
    grid-template-columns: 1fr 5fr;
    align-items: end;
    padding-bottom: 10px;

    color: #fff;


    h1 {
        font: {
            family: 'NotoSansKR', 'NotoSans', sans-serif;
            weight: bold;
            size: 36px;
        }
        letter-spacing: 1.5px;
        text-align: center;
    }

    pre{
        font: {
            family: 'NotoSansKR', 'NotoSans', sans-serif;
            weight: normal;
            size: 18px;
        }
    }
}