@charset "utf-8";

@import '../assets/fonts/fonts.scss';



.mainLayout {
    width: 1440px;
    overflow-x: hidden;
    margin: 0 auto;

    display: grid;
    grid-template-columns: 1fr 5fr;

    background-color: #F2F2FD;

}

.mainLayout_ {
    width: 1440px;
    overflow-x: hidden;
    margin: 0 auto;
}

.main {
    background-color: #F2F2FD;
    min-height: 960px;
}



.sideBar {
    background-color: rgba($color: #00000000, $alpha: 0.0);
    box-shadow: 0px 0px 10px #ccc;

    background: linear-gradient(#104C7E, #8AA5EA 20%, #F8D9EC);

    height: 700px;

    display: flex;
    align-items: flex-start;
    justify-content: flex-end;

    // box-shadow: inset -2px -1px 2px #bbb;

    border-bottom-right-radius: 30px;


    ul {
        list-style: none;
        background-color: rgba($color: #00000000, $alpha: 0.0);
        
        margin-top: 75px;


        width: 90%;
    
        li {
            width: 100%;
            height: 90px;
            margin: -20px 0;

            display: grid;
            grid-template-columns: 1.8fr 3fr;
            gap: 20px;
            align-items: center;

            img {
                margin-left: auto;
                
            }
            
        }

        
        
    }
}

.sideBar a{
    font-size: 16px;
    font-family: 'NotoSansKR', 'NotoSans', sans-serif;
    font-weight: bold;
    text-decoration: none;

    color: #000;
}

.sideBar a:hover{
    color: #003194;
}


.selected{
    background-color: rgba($color: #00000000, $alpha: 0.0);
    color: #000;

    background-image: url(../assets/images/sideBarSelected.svg);


    
    background-position: 26.5px;

    background-size: 190px;

    background-repeat: no-repeat;


    a{
        color: #000;
    }
}

