@charset "utf-8";


// Inter
@font-face {
    font-family: 'Inter';
    src: url('./Inter-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Inter';
    src: url('./Inter-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

// NotoSans
@font-face {
    font-family: 'NotoSans';
    src: url('./NotoSans-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NotoSans';
    src: url('./NotoSans-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

// NotoSansKR
@font-face {
    font-family: 'NotoSansKR';
    src: url('./NotoSansKR-Regular.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NotoSansKR';
    src: url('./NotoSansKR-Bold.otf') format('opentype');
    font-weight: bold;
    font-style: normal;
}