footer {
    width:  1440px;
    margin: 0 auto;
    background-color: #1f1f1f;

    font-size: 16px;


    padding: 10px 0 15px 0;

    display: flex;
    flex-direction: row;
    justify-content: space-around;
}


.footer_text {
    display: flex;
    flex-direction: column;
    justify-content: space-around;


    color: rgba(255,255,255,.5);
    

    .footer_link{
        list-style: none;

        display: flex;
        flex-direction: row;

        a{
            color: rgba(255,255,255,.5);
            text-decoration: none;
            transition: 0.3s;
        }

        a:hover {
            color: #fff;
            font-weight: bold;
        }

        img{
            margin: 0 20px;
        }
    }



    .footer_static{
        display: flex;
        flex-direction: column;
        gap: 20px;

        div {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }
}


.footer_img {
    display: flex;
    flex-direction: column;
    gap: 10px;
}


.linksite{
    width: 100%;
    padding: 15px;
    border: 1.5px solid rgba(255,255,255,.5);

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    


    a {
        color: rgba(255,255,255,.5);
        text-decoration: none;
    }

    
}